.body {
  width: 190px;
  position: absolute;
  border-radius: 4px;
  border: 1px solid #b1b1b1;
  background-color: #ffffff;
  margin-left: 25px;
  color: #000000;
  margin-top: 20px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
  padding: 10px 15px;
  text-indent: 0px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-transform: none;
  z-index: 1;
}

.item {
  cursor: pointer;
  display: flex;
}

.checkbox {
  width: 15px;
  height: 15px;
  border-radius: 3px;
  margin-top: 2px;
  border: 1px solid #dee2e6;
}

.activeCheckbox::after {
  content: url('../../../assets/icons/check.svg');
  position: absolute;
  width: 15px;
  height: 15px;
  margin-top: -3px;
  margin-left: 2px;
}

.activeCheckbox {
  background-color: #008662;
  position: relative;
}

.footer {
  text-align: right;
  display: flex;
}

.label {
  text-indent: 10px;
  color: #656565;
}

.icon {
  width: 15px;
  height: 15px;
  margin-right: 7px;
}
