.selectedRow > td {
  background-color: #116c53;
  color: #fafafa;
  border: none;
}

.row {
  border-bottom: 1px solid #e2e8f0;
}

.row td {
  background: #ffffff;
  color: #656565;
}

.row td,
.selectedRow > td {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow-x: hidden;
  text-indent: 9px;
  height: 55px;
  font-size: 13px;
  line-height: 16px;
  cursor: pointer;
}

.row td:first-child,
.selectedRow > td:first-child {
  text-indent: 20px;
}

.row > td > img {
  filter: brightness(0) saturate(100%) invert(25%) sepia(57%) saturate(6445%) hue-rotate(159deg) brightness(96%) contrast(104%);
}

.selectedRow > td > img {
  filter: brightness(0) invert(1);
}

.isHighlighted {
  border-right: 2px solid #008662;
}
