.box {
    position: relative;
    max-width: 364px;
    min-width: 364px;
    height: calc(100vh - 127px);
    overflow-y: auto;
    margin-left: 15px;
    border-radius: 6px;
    padding: 15px 18px;
    padding-left: 23px;
    background-color: #ffffff;
    border: 2px solid #008662;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
}

.closeIcon {
    right: 0;
    width: 12.73px;
    height: 12.73px;
    position: absolute;
    filter: brightness(0) invert(0.7);
    cursor: pointer;
}

.title {
    font-weight: 300;
    font-size: 30px;
    margin-top: 30px;
    margin-bottom: 10px;
    overflow-x: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.body {
    height: calc(100vh - 300px);
    overflow-y: auto;
    padding-right: 5px;
}

.details {
    display: flex;
}

.detailColumn {
    width: 100%;
    overflow-x: hidden;
    padding-right: 10px;
}

.detailName {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow-x: hidden;
    font-weight: 600;
    font-size: 12px;
    color: #6D6D73;
}

.detailName > span {
    color: #B1B1B1;
    font-weight: 400;
}

.form {
    margin-top: 5px;
    margin-bottom: 10px;
    width: 100%;
}

.formInput {
    margin-bottom: 10px;
    position: relative;
    width: 100%;
}

.formInputLabel {
    font-size: 14px;
    color: #6D6D73;
    font-weight: 600;
    overflow-x: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
}

.formInputLabel > span {
    color: #787E82;
    font-weight: 400;
}

.autoCompleteWrapper {
    position: absolute;
    top: 0;
    margin-top: 52px;
    width: 100%;
    background-color: #FFFFFF;
    border: 1px solid #d3d2d2;
    border-radius: 0px 0px 4px 4px;
    z-index: 1;
    max-height: 150px;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 10px;
    padding-right: 3px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.15);
}

.autoCompleteWrapper::-webkit-scrollbar-thumb,
.body::-webkit-scrollbar-thumb {
    background-color: #D2D2D2;
}

.autoCompleteWrapper::-webkit-scrollbar,
.body::-webkit-scrollbar {
    background-color: #D2D2D233;
    width: 14px;
}

.autoCompleteItemCategoryName,
.autoCompleteItemProfileName {
    font-weight: 400;
    font-size: 13px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
}

.autoCompleteItemCategoryName {
    color: #787E82;
    font-weight: 600;
    font-size: 14px;
    margin-top: 3px;
    margin-bottom: 0px;
}

.autoCompleteItemCategoryName:first-child {
    margin-top: 0px;
}

.autoCompleteItemProfileName {
    color: #343A40;
}

.autoCompleteItemProfileName:hover {
    background-color: #B1B1B180;
    cursor: pointer;
}

.footer {
    text-align: center;
    margin-top: 20px;
}

.footer > i {
    cursor: pointer;
}

.footer i + i {
    margin-left: 30px;
}

.trashcanIcon, .plusCircleIcon {
    cursor: pointer;
    margin-top: 12px;
    margin-left: 10px;
}

.trashcanIcon {
    margin-left: 8px;
}

.errorText {
    color: #FF7878;
    font-size: 12px;
    margin-top: -5px;
    margin-bottom: 5px;
}

.dateRangePickerLabel {
    width: 270px;
    color: #787E82;
    font-weight: 400;
    font-size: 14px;
}

.dateRangePickerLabel:last-child {
    width: 100%;
}