.body {
  position: absolute;
  border-radius: 4px;
  border: 1px solid #b1b1b1;
  width: 232px;
  background-color: #ffffff;
  margin-left: 25px;
  margin-top: 20px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
  padding: 17px;
  text-indent: 0px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-transform: none;
  z-index: 1;
}

.list {
  max-height: 117px;
  overflow-y: auto;
}

.list::-webkit-scrollbar-thumb {
  background-color: #d2d2d2;
  box-shadow: inset 0 0 0 3px #f4f6f8;
  border-radius: 4px 4px 4px 4px;
}

.list::-webkit-scrollbar {
  background-color: #f4f6f8;
  width: 14px;
  padding: 5px;
  border-radius: 0px 4px 4px 0px;
}

.item {
  display: flex;
  padding: 2px;
  cursor: pointer;
}

.item:hover {
  background-color: #ced4da;
}

.checkbox {
  width: 18px;
  height: 17px;
  border-radius: 3px;
  margin-top: 2px;
  border: 1px solid #dee2e6;
}

.activeCheckbox::after {
  content: url('../../../assets/icons/check.svg');
  position: absolute;
  margin-top: -2px;
  margin-left: 3px;
}

.activeCheckbox {
  background-color: #008662;
  position: relative;
}

.label {
  line-height: 20px;
  width: 100%;
  text-indent: 10px;
  color: #000000;
  font-size: 12px;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}

.footer {
  text-align: right;
}
