.body {
    position: absolute;
    border-radius: 4px;
    border: 1px solid #b1b1b1;
    width: 232px;
    background-color: #ffffff;
    color: #000000;
    margin-left: 25px;
    margin-top: 20px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
    padding: 17px;
    text-align: end;
    z-index: 1;
}

.select {
    width: 100%;
    border-radius: 4px;
    background-color: #008662;
    text-align: start;
    padding: 7.5px 15px;
    color: #ffffff;
    border: none;
    position: relative;
}

.select:after {
    position: absolute;
    right: 0;
    margin-right: 15px;
    content: url('../../../assets/icons/caret-down.svg');
}

.options {
    position: absolute;
    margin-top: 5px;
    left: 0;
    border: 1px solid #b1b1b1;
    background-color: #ffffff;
    width: 100%;
    z-index: 1;
}

.optionItem {
    color: #000000;
    height: 25px;
    line-height: 25px;
    text-indent: 15px;
}

.optionItem:hover {
    background-color: #ced4da;
}

.input {
    margin-top: 15px;
    width: 100%;
    height: 32px;
    padding: 5px 15px;
    border-radius: 4px;
    border: 1px solid #ced4da;
    color: #6d6d73;
}

.input::placeholder {
    color: #b1b1b1;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

.btnClear,
.btnFilter {
    float: right;
    width: 77px;
    height: 36px;
    border-radius: 4px;
    padding: 0px 24px;
    border: none;
}

.btnFilter {
    background-color: #008662;
    color: #ffffff;
}

.btnClear {
    background-color: transparent;
    color: #6d6d73;
}

.select:focus,
.input:focus,
.btnClear:focus,
.btnFilter:focus {
    outline: none;
}