.boxShadow {
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
  width: 100%;
  border-radius: 6px;
}

.table {
  border-radius: 6px;
}

.tableBodyWrapper {
  background-color: transparent;
  overflow-y: auto;
  height: calc(100vh - 300px);
}

.tableBodyWrapper::-webkit-scrollbar-thumb {
  background-color: #cbcbcb;
  box-shadow: inset 0 0 0 3px #ffffff;
  border-left: 1px solid #dee3eb;
  border-right: 1px solid #dee3eb;
}

.tableBodyWrapper::-webkit-scrollbar-track {
  border-right: 1px solid #dee3eb;
  border-left: 1px solid #dee3eb;
}

.tableBodyWrapper::-webkit-scrollbar {
  background-color: #ffffff;
  width: 14px;
  padding: 5px;
}

.tbody {
  background-color: #ffffff;
}

.noDataVerbiage {
  color: #008662;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
}

.tbodyDiv {
  height: 100%;
  width: 100%;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.faCircleNotchSpin {
  font-size: 50px;
  color: #13ad83;
}

.table, .thead, .tbody {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.table{
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);

}