.searchWrapper {
  display: flex;
  width: 100%;
  margin-bottom: 5px;
}

.search {
  height: 28px;
  width: 100%;
  border: 1px solid #b1b1b1;
  border-right: none;
  border-radius: 4px 0px 0px 4px;
  font-size: 12px;
  text-indent: 5px;
}

.imageDiv{
width: 100%;
}

.search:focus {
  outline: none;
}

.timesIcon {
  content: url('../../assets/icons/times.svg');
  width: 12.7px;
  height: auto;
  margin-top: 6px;
  margin-left: 10px;
  cursor: pointer;
}

.searchIcon {
  content: url('../../assets/icons/magnifying-glass.svg');
  margin-top: 6px;
  margin-left: 5px;
  cursor: pointer;
}

.timesIcon:hover,
.searchIcon:hover {
  filter: brightness(0) saturate(100%) invert(30%) sepia(86%) saturate(1538%)
    hue-rotate(142deg) brightness(87%) contrast(101%);
}

.timesIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-right: none;
}

.searchIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-radius: 0px 4px 4px 0px;
  height: 28px;
}

.toggleAttributesTableIcon {
  cursor: pointer;
  margin-left: 5px;
  float: right;
  width: 30px;
  height: 28px;
}