.timeInput {
    display: flex;
    width: 100%;
    height: 33px;
    border: 1px solid #CED4DA;
    border-radius: 4px;
    position: relative;
}

.timeInput:hover {
    cursor: text;
}

.placeHolderInput {
    border: none;
    width: 100%;
    font-size: 12px;
    text-indent: 10px;
    border-radius: 4px;
    padding-top: 1px;
}

.placeHolderInput:focus {
    outline: none;
}

.smallerWidth {
    width: 15px;
}

.largerWidth {
    width: 19px;
}

.input {
    /* padding: 8px 13px; */
    color: #343a40;
    font-size: 12px;
    border: transparent;
    text-align: right;
    caret-color: transparent;
}

.smallSemiColon {
    color: #343a40;
    font-size: 12px;
    margin-top: 5px;
}

.input:focus {
    background-color: #3390ff;
    color: #FFFFFF;
}

.input::placeholder {
    color: #FFFFFF;
}

.input:focus {
    outline: none;
}

.readonlyInput {
    width: 100%;
    color: #343a40;
    font-size: 12px;
    line-height: 14.32px;
    border: none;
    margin-left: -3px;
}

.readonlyInput:focus {
    outline: none;
}

.icon {
    width: 30px;
    min-width: 30px;
    padding-top: 3px;
    cursor: pointer;
    position: relative;
    border-left: none;
    border-radius: 0px 4px 4px 0px;
    position: absolute;
    right: 0;
}

.clockIcon {
    width: 15px;
    height: 15px;
}

.timeDropdown {
    z-index: 2;
    position: absolute;
    border: 1px solid #CED4DA;
    background-color: #FFFFFF;
    padding: 7px;
    border-radius: 0px 0px 4px 4px;
    right: 0;
    margin-right: 20px;
    display: flex;
}

.hourInput, .minuteInput, .secondInput {
    width: 30px;
    text-align: center;
    border: 1px solid #CED4DA;
    border-radius: 4px;
    color: #6D6D73;
    font-weight: 400;
    font-size: 12px;
    height: 30px;
}

.hourInput:focus, .minuteInput:focus, .secondInput:focus {
    outline: none;
}

.semiColon {
    margin-top: 27px;
    margin-right: 5px;
    margin-left: 5px;
}

.chevGreyIcon {
    margin-left: 10px;
}

.noIcon {
    cursor: default;
}

.digitInput {
    height: 100%;
    margin-top: 2px;
}

.digitInput:first-child {
    margin-left: 10px;
}