.body {
  position: absolute;
  border-radius: 4px;
  border: 1px solid #b1b1b1;
  background-color: #ffffff;
  color: #000000;
  width: 850px;
  margin-top: 20px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
  padding: 17px 17px 17px 0px;
  text-indent: 0px;
  text-transform: none;
  display: flex;
  z-index: 1;
}

.list {
  width: 150px;
  margin-top: -10px;
}

.item {
  font-size: 12px;
  padding: 12px 25px;
  width: 100%;
  font-weight: 600;
  cursor: pointer;
}

.activeItem {
  background-color: #116c53;
  color: #ffffff;
  font-weight: 100;
}

.dateSection {
  border-left: 1px solid #e6edf3;
  padding: 0px 15px;
  text-align: center;
  width: 100%;
}

.dateRange {
  display: flex;
  padding-left: 50px;
  margin-bottom: -10px;
}

.dateRangeText {
  font-size: 14px;
  margin-left: 180px;
}

.dateRangeText:first-child {
  margin-right: 20px;
}

.dateRangeText:last-child {
  margin-left: 20px;
}

.arrowRightLongIcon {
  content: url('../../../assets/icons/arrow-right-long.svg');
  margin-top: -17px;
}

.totalDaysText {
  color: #808080;
  font-size: 12px;
}

.footer {
  width: 100%;
  text-align: right;
}
