.columns {
  color: #ffffff;
  background-color: #008662;
  height: 55.88px;
  font-weight: 300;
  font-size: 12px;
  text-transform: uppercase;
  text-indent: 9px;
  position: relative;
}

.columns:first-child {
  border-top-left-radius: 6px;
  text-indent: 20px;
}

.columns:last-child {
  border-top-right-radius: 6px;
}

.scrollWidthFiller {
  width: 14px;
  background-color: #008662;
  border-top-right-radius: 6px;
}

.label {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 100%;
  margin-right: 4px;
}

.filterIcons,
.clearAllFiltersIcon {
  cursor: pointer;
}

.clearAllFiltersIcon {
  filter: brightness(0) invert(1);
  width: 14px;
  height: 18px;
}

.icon {
  position: relative;
  width: 20px;
}

.icon img {
  position: absolute;
  left: 0;
  margin-top: 2px;
}

.icon:nth-child(3) img {
  margin-top: 3px;
}

.icon:nth-child(4) img {
  margin-top: -2px;
}
